<template>
   <b-navbar toggleable="lg" :type="themeMode ? 'dark': 'light'" variant="transparent">
    <b-navbar-brand :to="{ path: '/' }">
        <img src="../assets/name.png" class="d-inline-block align-top navbar-img" alt="Logo">
    </b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item :to="{ path: '/' }">Home</b-nav-item>

        <b-nav-item-dropdown text="About" right>
          <b-dropdown-item :to="{ path: '/myself' }">My Profile</b-dropdown-item>
          <b-dropdown-item :to="{ path: '/work' }">Work Experience</b-dropdown-item>
          <b-dropdown-item :to="{ path: '/skills' }">My Skills</b-dropdown-item>
          <b-dropdown-item :to="{ path: '/hobbies' }">My Hobbies</b-dropdown-item>
        </b-nav-item-dropdown>

        <b-nav-item :to="{ path: '/contact' }">Contact</b-nav-item>
      </b-navbar-nav>

      <b-navbar-nav class="ml-auto">
        
        <div>
          <div class="h2 mb-0 d-inline">
            <b-icon-sun></b-icon-sun>
          </div>  
          <label class="themeSwitch ml-1 mr-1 h3">
            <input type="checkbox" class="inputSwitch" v-model="themeMode">
            <span class="slider round spanSwitch"></span>
          </label>
          <div class="h2 mb-0 d-inline ">
            <b-icon-moon></b-icon-moon>
          </div>  
        </div>
      
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
export default {
  name: 'NavBar',
  data(){
    return {
      themeMode : false,
      root : ''
    }
  },
  props:{
    username: String,
     role: String
  },
  mounted: function() {
    this.root = document.documentElement;
    this.themeMode = this.$store.state.darkMode;
    this.changeTheme();
  },

  methods: {
    changeTheme(){
       if(this.themeMode){ //switch to dark mode
        this.root.style.setProperty("--bgColor", "#191919");
        this.root.style.setProperty("--fColor", "rgb(238, 237, 237)");
        this.root.style.setProperty("--btnColor", "purple");
      }
      else{ //switch to light mode
        this.root.style.setProperty("--bgColor", "rgb(238, 237, 237)");
        this.root.style.setProperty("--fColor", "#191919");
        this.root.style.setProperty("--btnColor", "purple");
      } 
    }
  },

  watch : {
    themeMode: function(darkTheme){ //switch themes
      this.$store.commit('switchTheme', darkTheme);  
      this.changeTheme();
    }  
  }
}
</script>

<style>

a.dropdown-item:hover{
  transition-duration: var(--tDuration);
  transition-timing-function: var(--ttFunction);
  background-color: var(--btnColor);
  color : var(--bgColor);
}
a.nav-link:hover{
  transition-duration: var(--tDuration);
  transition-timing-function: var(--ttFunction);
  text-decoration: underline;
}

.navbar-light .navbar-nav .nav-link{
  color: var(--fColor)!important;
}
.navbar-img{
  transition-duration: var(--tDuration);
  transition-timing-function: var(--ttFunction);
  max-width:220px;
  width:auto;
  height:auto;
  border-radius:15px;
  padding: 3px 7px;
  border: 3px double var(--fColor);
}

.navbar-img:hover{
  transition-duration: var(--tDuration);
  transition-timing-function: var(--ttFunction);
  box-shadow:
    0 0 2px 2px var(--fColor); /* outer cyan */
}

.nav-item:hover{
    color:purple!important;
}

/* The switch - the box around the slider */
.themeSwitch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.themeSwitch .inputSwitch {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

.inputSwitch:checked + .slider {
  background-color: purple;
}

.inputSwitch:focus + .slider {
  box-shadow: 0 0 1px purple;
}

.inputSwitch:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>