import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    darkMode : false,
  },
  getters: {
    showVariant: state => {
        if(!state.darkMode) return 'light';
        else return 'dark';
    }
  },
  mutations: {
    initialiseStore(state) {  //get data from local storage
      state.darkMode = localStorage.getItem('darkMode') == 'true' ? true : false;
      //add more
    },
    switchTheme : (state,value) =>{
      state.darkMode = value;
      localStorage.setItem('darkMode', value);
    },
  },
  actions: {
  },
  modules: {
  }
})
