<script>
// @ is an alias to /src

import Wave from "@foobar404/wave"
export default {
  name: 'Index',
  data(){
      return {
        show : true, //visualizer overlay
        playing : false
      }
  },
  mounted(){
        this.createWave();
        //check if audio playing
        if(!document.getElementById('backgroundMusic').paused){
            this.show=false;
            this.playing=true;
        }
  },
  methods:{
       createWave(){
            let color1 = getComputedStyle(document.body).getPropertyValue('--fColor');
            let color2 = getComputedStyle(document.body).getPropertyValue('--btnColor');
            let wave = new Wave();
            let options = {
                type:"shockwave",
                stroke: 2,
                colors: [color2,color1,color2,color1]
            };
            wave.fromElement("backgroundMusic","visualizer", options);
      
            this.$refs.visualizer.click();
            
        },
       playAudio(){
            if(document.getElementById('backgroundMusic').paused){
                document.getElementById('backgroundMusic').play();
                document.getElementById('backgroundMusic').volume = 0.1;
                this.show=false;
                this.playing=true;
            }
        },
        stopAudio(){
            if(this.playing){
                document.getElementById('backgroundMusic').pause();
                document.getElementById('backgroundMusic').currentTime = 0;
                this.show=true;
                this.playing=false;
            }
            else{
                this.playAudio();
            }
        }
  },
  computed:{
       showVariant(){
           return this.$store.getters.showVariant;
        },
        playIcon(){
            return this.playing ? 'stop-circle-fill' : 'play-circle-fill';
        }
  },
  watch: {
      showVariant(){
        this.createWave(); //recreate wave base on theme mode
      }
  }
}
</script>

