<template>
  <div id="app">
    <NavBar />
    <b-overlay :show="show" rounded="sm" class="mt-5" :variant="showVariant">
      <div class="body"> 
        <router-view/>
      </div>
    </b-overlay>
    <Footer />
    <audio id="backgroundMusic" loop>
      <source :src="require(`@/assets/background.mp3`)" type="audio/mp3">
    </audio>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import Footer from '@/components/Footer.vue'


export default {
  components :{
    NavBar,
    Footer
  },

  data(){
    return{
      show: true
    }
  },

  methods:{
   
  },

  beforeMount(){
    this.$store.commit('initialiseStore');
  },

  mounted(){
    this.show = false;
  },
  computed:{
    showVariant(){
           return  this.$store.getters.showVariant;
        }
  },
  watch:{
    $route (){
        this.show = true;
        this.$nextTick(()=>{
          this.show = false; //remove overlay after mounted new page
        });
    }
  }
}
</script>

